import http from '../../../../configs/httpConfig'
import { notifyError } from '../../../Toast'

export const getAllDataInit = () => ({
  type: 'GET_ALL_SQUAD_INIT'
})

export const clearData = () => ({
  type: 'CLEAR_DATA'
})

export const getAllCricketcontestFail = () => ({
  type: 'GET_ALL_USERS_FAIL'
})

export const getCricketSquad = (page) => {
  return async (dispatch) => {
    //dispatch(getAllDataInit())
    await http.get(`/cricket/squad/list?page=${page}`).then((response) => {
      dispatch({
        type: 'CRICKET_SQUAD',
        data: response.data.data
      })
    }).catch(() => {
      dispatch(getAllCricketcontestFail())
    })
  }
}

export const cricketSquadAdd = (add) => {
  return (dispatch) => {
    http
    .post('/cricket/squad/add', add)
    .then((response) => {
      dispatch({
        type: 'CRICKET_SQUAD_ADD',
        data: response.data
      })    
    })
    .catch((err) => console.log(err))
  }
}

export const deletesquad = (id) => {
  return (dispatch) => {
     return http.delete(`cricket/squad/delete/${id}`).then((response) => {
      notifyError('successfully deleted')
       dispatch({ type: 'DELETE_SQUAD', data: response.data })
       dispatch(getCricketSquad())
     })
     .catch(() => {
      notifyError('error')
     })
   } 
 }

