import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label

} from 'reactstrap'
import React, { Fragment, useEffect, useState } from 'react'
import Breadcrumbs from '@components/breadcrumbs'
import {  footballsquadviwe, footballsquadedit } from '../../store/actions'
import { useDispatch, useSelector } from 'react-redux'
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import '@styles/react/libs/flatpickr/flatpickr.scss'

const selectcategory = [
  { value: 'Bowler', label: 'Bowler' },
  { value: 'Wicket Keeper', label: 'Wicket Keeper' },
  { value: 'Batsman', label: 'Batsman' },
  { value: 'All Rounder', label: 'All Rounder' }
]

const VerticalForm = () => {
  const dispatch = useDispatch()
  const store = useSelector(state => state.squad)
  const {football_view_squad} = store
  console.log(football_view_squad)
 
  const [editsquadform, setEditsquadform] = useState()
  

  useEffect(() => {
    dispatch(footballsquadviwe)
  }, [football_view_squad])
  useEffect(() => {
    setEditsquadform({player_api:football_view_squad?.player_api, player_api_id:football_view_squad?.player_api_id, player_name:football_view_squad?.player_name, team_id:football_view_squad?.team_id, player_type:football_view_squad?.player_type, player_value:football_view_squad?.player_value, active:football_view_squad?.active })
  }, [football_view_squad])
  console.log(football_view_squad?.player_api)
  console.log(editsquadform)
  const onSubmit = (e) => {
    e.preventDefault()
    console.log(editsquadform)
    dispatch(footballsquadedit(football_view_squad.id, editsquadform))
  }
  
  const editcategory = (e) => {
    //console.log(e.target.value)
    setEditsquadform({...editsquadform, player_type:e.value})
  }

return (
  <Fragment>
    <Breadcrumbs breadCrumbTitle='Football Squad' breadCrumbParent='footballsquad' breadCrumbActive='Edit' />
       <Card>
         <CardHeader>
           <CardTitle tag="h4">Edit Cricket squad</CardTitle>
       </CardHeader>
         <CardBody>
           <Form>
             <Row>
            <Col sm="12">
                 <FormGroup>
                  <Label>Player Api</Label>
                   <Input
                     type="text"
                     name="name"
                    id="nameVertical"
                    value={editsquadform?.player_api}
                    onChange={(e) => setEditsquadform({ ...editsquadform, player_api: e.target.value })}
                     placeholder="Enter Product Name"
                   />
                 </FormGroup>
              </Col>
             <Col sm="12">
                <FormGroup>
                  <Label for="EmailVertical">Player Api Id</Label>
                  <Input
                     type="number"
                     name="Email"
                    id="EmailVertical"
                    value={editsquadform?.player_api_id}
                     onChange={(e) => setEditsquadform({ ...editsquadform, player_api_id: e.target.value })}
                     placeholder="Enter No of ID "
                  />
                </FormGroup>
               </Col>
               <Col sm="12">
                <FormGroup>
                  <Label for="EmailVertical">Player Name</Label>
                  <Input
                    type="Fixturename"
                    name="Email"
                   id="EmailVertical"
                     value={editsquadform?.player_name}
                    onChange={(e) => setEditsquadform({ ...editsquadform, player_name: e.target.value })}
                    placeholder="Enter No of ID "
                  />
                </FormGroup>
              </Col>  
              <Col sm="12">
                <FormGroup>
                   <Label>Team Id</Label>
                   <Input
                     type="text"
                    name="name"
                    id="nameVertical"
                    value={editsquadform?.team_id}
                    onChange={(e) => setEditsquadform({ ...editsquadform, team_id: e.target.value })}
                   placeholder="Enter Product Name"
                  />
                </FormGroup>
              </Col>
              <Col sm="12">
                 <FormGroup>
                   <Label for="nameVertical">player Type</Label>
                  <Select
                   theme={selectThemeColors}
                     className="react-select"
                     classNamePrefix="select"
                     defaultValue={selectcategory[0]}                 
                     onChange={(e) => editcategory(e)}
                    options={selectcategory}
                    isClearable={false}
                  />
                </FormGroup>
               </Col>
              
             <Col  sm="12">
                <FormGroup>
                   <Label for="nameVertical">player_value</Label>
                  <Input
                     type="text"
                    name="name"
                     id="nameVertical"
                 value={editsquadform?.player_value}
                    onChange={(e) => setEditsquadform({ ...editsquadform, player_value: e.target.value })}
                    placeholder="Enter Product Name"
                  />
                 </FormGroup>
              </Col>
               <Col sm="12">
                 <FormGroup>
                  <Label for="nameVertical">Active</Label>
                   <Input
                    type="text"
                     name="name"
                     id="nameVertical"
                     value={editsquadform?.active}
                     onChange={(e) => setEditsquadform({ ...editsquadform, active: e.target.value })}
                     placeholder="Enter Product Name"
                   />
                </FormGroup>
               </Col>
             <Col sm="12">
                 <FormGroup className="d-flex mb-0">
                  <Button.Ripple
                     className="mr-1"
                     color="primary"
                     type="submit"
                     onClick={(e) => onSubmit(e)}
                  >
                     Submit
                  </Button.Ripple>
                  {/* <Button.Ripple
                   outline
                     color="secondary"
                     type="reset"
                     onClick={(e) => onclear(e)}
                   >
                     Reset
                   </Button.Ripple> */}
                </FormGroup>
              </Col>
             </Row>
          </Form>
        </CardBody>
       </Card>
       </Fragment>
     )
}
export default VerticalForm
